  body{
    font-family: 'Zen Maru Gothic', sans-serif;
  }
  .section {
  padding-top: 20px;
  padding-bottom: 20px;
  @media screen and (min-width: 1440px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
  .container {
    margin-left: auto;
    margin-right: auto;
  
    width: 100%;
  
    @media screen and ( min-width: 320px){
      max-width: 480px;
      
      padding-left: 20px;
      padding-right: 20px;
    }
  
    @media screen and ( min-width: 768px){
      max-width: 768px;

      padding-left: 32px;
      padding-right: 32px;
    }
  
    @media screen and ( min-width: 1440px){
      min-width: 1440px;

      padding-left: 32px;
      padding-right: 32px;
    }
   }

   .button{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 277px;
    height: 56px;
    border: none;
    color: var(--second-title-color);
    background-color: #F5F5FA;
    box-shadow: -10px -10px 20px #FFFFFF, 10px 10px 20px rgba(72, 121, 150, 0.1), 5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #FFFFFF;
    border-radius: 30.5px;

    font-weight: 700;
    font-size: 16px;
    line-height: calc(24 / 16);
    letter-spacing: 0.1em;
    text-transform: uppercase;

    transition: var(--transition-function);
  }

.visually-hidden {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    margin: -1px;
  }
  

  .backdrop.is-hidden{
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }